/* css reset */
*{
    box-sizing: border-box;
}
*::before,*::after{
    box-sizing: border-box;
}
*{
    margin: 0;
    padding: 0;
}
ul,ol{
    list-style: none;
}
a{
    color: inherit;
    text-decoration: none;
}
img{
    max-width: 130%;
    max-height: 130%;
}

.icon {
    width: 1em; height: 1em;
    vertical-align: -0.15em;
    fill: currentColor;
    overflow: hidden;
 }
/* style */
body{
    background-image: url('./image/backgroundimage.jpeg');
    background-size: cover;
}

@media(min-width:500px){
.globalHeader{
    /* border: 1px solid red; */
    margin: 60px 0;
}
}
.searchForm{
    display:flex;
    justify-content: space-between;
}
@media (min-width:500px){
    .searchForm{
        max-width: 400px;
        margin-left: auto;
        margin-right: auto;
    }
}
.searchForm > input{
    width: 100%;
    height: 40px;
    margin-right: 10px;
    padding: 0 10px;
    border: 1px solid #ddd;
    border-radius: 4px;
}
.searchForm > button{
    white-space: nowrap;
    padding: 0 28px;
    border: none;
    border-radius: 4px;
    background: #0282b9;
    color: white;
    font-size: 16px;
}

.globalMain{
    max-width: 900px;
    margin-left: auto;
    margin-right: auto;
}

.siteList{
    display: flex;
    flex-wrap: wrap;
    margin: 20px;
    justify-content: space-between;
}

@media(min-width:500px){
    .siteList{
        margin-left: 0;
        margin-right: -25px;
        justify-content: flex-start;
    }
}

.siteList > li{
    margin-bottom: 10px;
}
@media(min-width:500px){
    .siteList > li{

        margin-right: 25px;
    }
}
.siteList .site{
    width: 160px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    background-color: white;
    border: 1px solid #ddd;
    border-radius: 4px;
    padding: 20px 0;
    position: relative;
    cursor: pointer;
}
.siteList .site > .logo{
    width: 64px;
    height: 64px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 64px;
    text-transform: uppercase;
}
.siteList .site > .link{
    font-size: 14px;
    margin-top: 4px;
}
.siteList .site > .close{
    position: absolute;
    right: 10px;
    top: 5px;
    display: none;
    cursor: default;
}
.siteList .site:hover > .close{
    display: block;
}
.siteList .addButton{
    border: 1px solid #ddd;
    background: white;
    width: 160px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 20px 0;
    cursor: pointer;
}
.siteList .addButton .icon{
    width: 60px;
    height: 60px;
}
.siteList .addButton .text{
    font-size: 14px;
    margin-top: 4px;
}
.siteList .addButton .icon-wrapper{
    /* border: 1px solid red; */
    width: 64px;
    height: 64px;
    display: flex;
    justify-content: center;
    align-items: center;
}
